.dp_steps {
  display: flex;
  flex-wrap: wrap;
  list-style: none;

  padding: 0px;
  margin: 1em 0;
}

.dp_step_label {
  position: relative;

  &.active {
    .btn {
      background-color: #ff5722;
      border-color: #ff5722;
    }

    .btn:hover {
      background-color: #f4511e;
      border-color: #f4511e;
    }

    &:not(:last-child) .btn:after {
      border-left: 10px solid #ff5722;
    }

    &:not(:last-child) .btn:before {
      border-left: 10px solid #ff5722;
    }

    &:not(:last-child) .btn:hover:after {
      border-left: 10px solid #f4511e;
    }

    &:not(:last-child) .btn:hover:before {
      border-left: 10px solid #f4511e;
    }
  }

  &:not(:last-child) .btn:after {
    content: " ";
    display: block;
    width: 0;
    height: 0;
    border-top: 17px solid transparent;
    border-bottom: 17px solid transparent;
    border-left: 10px solid white;
    position: absolute;
    top: 50%;
    margin-top: -17px;
    left: 100%;
    z-index: 3;
  }

  &:not(:last-child) .btn:before {
    content: " ";
    display: block;
    width: 0;
    height: 0;
    border-top: 17px solid transparent;
    border-bottom: 17px solid transparent;
    border-left: 10px solid rgb(173, 173, 173);
    position: absolute;
    top: 50%;
    margin-top: -17px;
    margin-left: 1px;
    left: 100%;
    z-index: 3;
  }

  /** The Spacing **/
  .btn {
    padding: 6px 12px 6px 24px;
  }

  &:first-child .btn {
    padding: 6px 6px 6px 10px;
  }

  &:last-child .btn {
    padding: 6px 18px 6px 24px;
  }

  &:not(:last-child) .btn:after {
    border-left: 10px solid #5bc0de;
  }

  &:not(:last-child) .btn:before {
    border-left: 10px solid #46b8da;
  }

  &:not(:last-child) .btn:hover:after {
    border-left: 10px solid #39b3d7;
  }

  &:not(:last-child) .btn:hover:before {
    border-left: 10px solid #269abc;
  }
}

.dp_steps_buttons {
  display: flex;
}

.dp_step_prev {
  padding-left: .2em;
}

.dp_step_next {
  padding-right: .2em;
}
