.dp_hidden {
  display: none !important;
}

.dp_away {
  position: fixed;
  top: -10000px;
  left: -10000px;
}

.dp_relative {
  position: relative;
}

.dp-btn-small {
  padding: .5rem;

  i.material-icons {
    margin-right: 0;
  }
}

.dp_btn {
  background: none;
  border: none;
  cursor: pointer;
}

.dp-btn-round {
  border-radius: 50%;
  background-color: #fff;
  display: inline-block;
  width: 36px;
  height: 36px;
  text-align: center;
  &:focus {
    outline: none !important;
    box-shadow: 0 0 0 1px #000;
  }
}

.dp_link {
  color: #25b9d7;
}
