.dp_input_container {
  .ui-corner-all {
    border-radius: 0;
  }

  .ui-spinner:focus-within {
    outline: .1875rem solid #2fb5d2;
  }

  .ui-spinner-input {
    margin: 0;
    outline: none;
    height: 2.5rem;
    padding: 0 10px;
  }

  .ui-spinner-button {
    font-size: 18px;
    width: 24px;
    cursor: pointer;

    position: absolute;
    right: 0;

    display: flex;
    justify-items: center;
    align-items: center;

    &.ui-spinner-up {
      box-sizing: border-box;
      border-bottom: 1px solid #D3D3D3;
    }

    &:hover {
      background-color: #EEE;
    }

    &:active {
      background-color: #AAA;
    }
  }

  .ui-button, .ui-button-text, .ui-spinner-button .ui-icon {
    background: none;
  }

  .ui-spinner-button .ui-button-text {
    padding: 0;
  }

  .ui-button-text {
    padding: .2em;
  }

  .ui-button:active {
    background-color: #AAA;
  }
}