.dp_container {
  button:focus {
    outline: 3px solid -webkit-focus-ring-color;
    outline-offset: 3px;
  }
}

.dp_input_container {
  button i {
    font-size: 18px;
  }

  button.btn-xs {
    padding: .25rem .5rem;
    font-size: .875rem;
    border-radius: .2rem;
  }
}

.dp_disabled {
  cursor: not-allowed;
  filter: brightness(0.9);
}
